*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  background-color: #222;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-style: normal;
}
.card{
  width: 470px;
  margin: 100px auto 0px;
  background:linear-gradient(158deg , #00feba , #5b5480);
  padding: 30px;
  border-radius: 10px;
}
.search{
  width: 90%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
}
input{
  width: 85%;
  padding: 10px 15px;
  border: none;
  outline: none;
  background-color: aliceblue;
  border-radius: 20px;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  color: #373737;
}
.secrch_icon{
  width: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: aliceblue;
}
.secrch_icon img{
  cursor: pointer;
}
.weather{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
h1{
  font-size: 70px;
  color: #fff;
  margin: 15px 0px 0px;
}
h2{
  font-size: 42px;
  color: #fff;
}
.detailes{
  margin: 30px 0px 0px;
  display: flex;
  justify-content: space-between;
}
.content1{
  display: flex;
}
.text_content{
  margin-left: 10px;
  color: #fff;
}
.error{
  text-align: center;
  margin-left: -40px;
  color: #fff;
  font-weight: 400;
  display: none;
}
@media (max-width: 550px) {
  .card{
      width: 300px;
      padding: 20px;
  }
  .search{
      width: 90%;
      display: flex;
      justify-content: space-between;
      margin-bottom: 15px;
  }
  input{
      width: 70%;
      padding: 10px 15px;
      border: none;
      outline: none;
      background-color: aliceblue;
      border-radius: 20px;
      font-family: "Roboto", sans-serif;
      font-size: 16px;
      color: #373737;
  }
}
